<script>
    import * as url from '../until/api'
    import * as until from '../until/until'
    import Header from '@components/Header'
    import Footer from '@components/Footer'
    import {mapActions} from 'vuex'

    window.onresize = () => {
        // console.log(until.getClientHeight());
        if (until.getClientHeight() > document.getElementById('home').offsetHeight) {
            document.getElementById("home").style.height = until.getClientHeight() + 'px'
        }
    }
    export default {
        name: "Home",
        components: {
            Header,
            Footer,
        },
        data() {

            var validateAccount = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }
                callback();
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }
                callback();
            };
            return {
                ruleForm: {
                    pass: '',
                    account: '',
                },
                rules: {
                    account: [
                        {validator: validateAccount, trigger: 'blur'}
                    ],
                    pass: [
                        {validator: validatePass, trigger: 'blur'}
                    ],
                },
                isVerify: false,
                activeName: 'first',
                previewShow: false,
                previewText: 'previewCla',
                loginType: this.$store.state.loginType,
                tableTotal: 0,
                listData: [{name: 'jack', email: '10577507@qq.com', tel: '15632486433', isUsed: true}, {
                    name: 'Rose',
                    email: '105507@163.com',
                    tel: '18832486437',
                    isUsed: false
                }],
                tableType: 1,
                tableShow: true,
                currentPage: 1,
                listCurrentPage: 1,
                dropdownTitle: 'Linked Repositories',
                isEmail: false,
                email: '',
                code: '',
                gitee_client_id: this.$store.state.gitee_client_id,
                gitee_client_secret: this.$store.state.gitee_client_secret,
                gitee_redirect_uri: this.$store.state.gitee_redirect_uri,
                github_client_id: this.$store.state.github_client_id,
                github_client_secret: this.$store.state.github_client_secret,
                github_redirect_uri: this.$store.state.github_redirect_uri,
                access_token: this.$store.state.access_token,
                refresh_token: this.$store.state.refresh_token,
                listDialogVisible: false,
                checkClaDialogVisible: false,
                unLinkDialogVisible: false,
                editDialogVisible: false,
                menuVisible: false,
                claName: 'test',
                repositoryName: 'ooo/test',
                shareGistChecked: false,
                claOptions: [{value: '0', label: 'test', text: ''}, {
                    value: '1',
                    label: 'share',
                    text: '   ' +
                        '' +
                        '    ' +
                        '              ' +
                        '' +
                        '' +
                        '' +
                        '              ' +
                        '' +
                        '' +
                        '' +
                        ''
                },],
                claValue: '0',
                claChoose: false,
                linkDialogVisible: false,
                shareDialogVisible: false,
                createCLADialogVisible: false,
                authorizeDialogVisible: false,
                fileNumber: '',
                lineNumber: '',
                gistUrl: '',
                orgOptions:[{value:'0',label:'myOrg'}],
                orgValue:'0',
                repositoryOptions: [{value: '0', label: 'test',},],
                repositoryValue: '0',
                repositoryChoose: '',
                showConfigForm: false,

                tableData: [{repository: 'ooo', cla: 'test', sharedGist: 'Yes', contributors: '0',}, {
                    repository: 'ooo',
                    cla: 'test',
                    sharedGist: 'No',
                    contributors: '0',
                }],
                tableDataOther: [{repository: 'ooo', cla: 'test', sharedGist: 'Yes', contributors: '0',},],
                home: {
                    height: '',
                },
                user: {
                    userName: '',
                    userId: '',
                    isAuthorize: false,
                },
            }
        },
        methods: {
            ...mapActions(['setLoginUserAct', 'setTokenAct']),
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            tabsHandleClick(tab, event) {
                console.log(tab, event);
            },
            /**/
            getOrgPermission() {
                if (this.loginType === '0') {
                    window.location.href = 'https://github.com/login/oauth/authorize?client_id=d86f4915398dad23bffc&redirect_url=http://localhost:8080/home&scope=repo';
                }
                // this.$axios({
                //     url:'/user/orgs',
                //     methods:'get'
                // }).then(res => {
                //     console.log(res);
                // }).catch(err => {
                //     console.log(err);
                // })
            },

            /**/
            listChangePage(page) {
                console.log(page);
            },
            /**/
            changePage(page) {
                console.log(page);
            },
            handleCommand(command) {
                // this.$message('click on item ' + command);
                this.dropdownTitle = command
                switch (command) {
                    case '':
                        this.tableType = 1;
                        this.tableShow = true;
                        this.currentPage = 1;
                        this.tableTotal = this.tableData.length
                        console.log(this.tableShow);
                        // this.getLinkedRepositories();
                        break;
                    case '':
                        this.tableType = 2;
                        this.currentPage = 1;

                        this.tableShow = false;
                        console.log(this.tableShow);
                        this.tableTotal = this.tableDataOther.length;

                        break;
                    case '':
                        this.tableType = 3;
                        this.currentPage = 1;

                        this.tableShow = false;
                        console.log(this.tableShow);
                        this.tableTotal = this.tableDataOther.length;

                        // this.getCompanyRepositories();
                        break;
                    case '':
                        this.tableType = 4;
                        this.tableShow = false;
                        this.currentPage = 1;

                        console.log(this.tableShow);
                        this.tableTotal = this.tableDataOther.length;
                        // this.getCompanyPersonRepositories();
                        break;


                }
            },
            /**/
            getLinkedRepositories() {
                console.log('getLinkedRepositories');
                let obj = {
                    userName: this.user.userName
                };
                this.$axios({
                    url: url.getLinkedRepositories,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                    if (res.data.code === 200) {
                        this.tableData = res.data.data
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /**/
            getPersonalSigned() {
                console.log('getPersonalRepositories');
                let obj = {
                    userName: this.user.userName
                };
                this.$axios({
                    url: url.getPersonalRepositories,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                    if (res.data.code === 200) {
                        this.tableData = res.data.data
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /**/
            getCompanyRepositories() {
                console.log('getCompanyRepositories');
                let obj = {
                    userName: this.user.userName
                };
                this.$axios({
                    url: url.getCompanyRepositories,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                    if (res.data.code === 200) {
                        this.tableData = res.data.data
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /**/
            getCompanyPersonRepositories() {
                console.log('getCompanyPersonRepositories');
                let obj = {
                    userName: this.user.userName
                };
                this.$axios({
                    url: url.getCompanyPersonRepositories,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                    if (res.data.code === 200) {
                        this.tableData = res.data.data
                    }
                }).catch(err => {
                    console.log(err);
                })
            },

            newWindow() {
                // window.open('https://github.com/ouchengle/Test','_black')
                window.open('https://github.com/ouchengle')
            },
            /**/
            openLinkDialog() {
                (this.repositoryChoose && this.claChoose && this.isEmail) && (this.linkDialogVisible = true)
            },


            /**/
            verifyEmail() {
                var email = this.email;
                console.log(email);
                var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
                if (reg.test(email)) {
                    this.isEmail = true
                } else {
                    // this.$message({
                    //     type: 'error',
                    //     message:'Please enter the correct email',
                    //     center:true
                    // });
                    this.isEmail = false

                    return false
                }
            },
            /**/
            unLinkRepository() {
                this.linkDialogVisible = false;
                let obj = {
                    repositoryValue: this.repositoryValue,
                    claValue: this.claValue,

                };
                this.$axios({
                    url: url.unLinkRepository,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                    if (res.data.code === 200) {
                        this.repositoryOptions = res.data.data
                        this.$message.success('')
                        this.unLinkDialogVisible = false
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /**/
            linkRepository() {
                this.linkDialogVisible = false;
                let obj = {
                    repositoryId: this.repositoryOptions[this.repositoryValue].id,
                    claId: this.claOptions[this.claValue].id,
                    email: this.email,
                    platform:this.platform,
                    orgId:this.orgOptions[this.orgValue].id
                };
                this.$axios({
                    url: '/api'+url.linkRepository,
                    methods: 'post',
                    data: obj,
                }).then(res => {
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },

            /*CLA*/
            checkCla() {
                console.log("checkCla");
                this.$router.push('/checkCla')
            },
            /*cla*/
            editHandleClick(index) {
                console.log(index);
                this.editDialogVisible = true
            },
            /*CLA*/
            toCreateCLA() {
                this.$router.push('/createCLA')
            },
            /*cla*/
            changeCla(value) {
                this.claValue = value
                this.claChoose = true;
                console.log(value);
                this.previewText = this.claOptions[value].text;
            },
            /*dialog*/
            createCLA() {
                console.log("authorize");
                this.createCLADialogVisible = true
            },
            /*CLA*/
            authorize() {
                console.log("authorize");
                this.authorizeDialogVisible = true
                /*dialog*/

            },

            /**/
            changeRepository(value) {
                console.log(value);
                this.repositoryChoose = true;
                this.repositoryValue = value
            },
            getRepositoriesOfOrg(org,orgPath){
                console.log("getRepositoriesOfOrg");
                let obj = {access_token: this.access_token, org: `${orgPath}/login`, page: 1, per_page: 10,type:'all'};
                this.$axios({
                    url: url.getRepositoriesOfOrg,
                    params: obj,
                }).then(res => {
                    console.log(res);
                    if (res.status === 200) {
                        this.repositoryOptions=[];
                        res.data.forEach((item, index) => {
                            this.repositoryOptions.push({value: index, org:org,repoName:item.name,label: `${org}/${item.name}`});
                        })
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /**/
            getOrgsInfo() {
                console.log("getOrgsInfo");
                let obj = {access_token: this.access_token, admin: true, page: 1, per_page: 10};
                this.$axios({
                    url: url.getOrgsInfo,
                    methods: 'get',
                    params: obj,
                }).then(res => {
                    console.log(res);
                    if (res.status === 200) {
                        // this.repositoryOptions = res.data.data
                        this.orgOptions=[];
                        res.data.forEach((item, index) => {
                            this.orgOptions.push({value: index, label: item.login,orgPath:item.url});
                            this.getRepositoriesOfOrg(item.login,item.url)
                        })
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /*cla*/
            getCLA() {
                console.log("getCLA");
                this.$axios({
                    url: '/api' + url.getClaInfo,
                    headers:{access_token: this.access_token,refresh_token:this.refresh_token}
                }).then(res => {
                    console.log(res);
                    if (res.data.length) {
                        this.claOptions=[];
                        res.data.forEach((item,index)=>{
                           this.claOptions.push({value:index,label:item.name,id:item.id,text:item.text})
                       })
                    }
                }).catch(err => {
                    console.log(err);
                })
            },
            /*cla*/
            closeConfigForm() {
                this.showConfigForm = false
                this.setClientHeight()
            },

            /*cla*/
            configCla() {
                this.showConfigForm = true;
                this.home.height = 'auto'
                this.getCLA()
                this.getOrgsInfo()
            },
            /**/
            setClientHeight() {
                // console.log(until.getClientHeight());
                this.$nextTick(() => {

                    until.getClientHeight() > document.getElementById('home').offsetHeight ?
                        this.home.height = until.getClientHeight() + 'px' :
                        this.home.height = document.getElementById('home').offsetHeight

                })
            },
            /**/
            change(value) {
                console.log(value);
                this.value = value;
            },
            /*cookie*/
            getCookieData() {
                console.log('getCookieData');
                let cookieArr = document.cookie.split('; ')
                let access_token, refresh_token = '';
                cookieArr.forEach((item, index) => {
                    let arr = item.split('=');
                    arr[0] === 'access_token' ? access_token = arr[1] : arr[0] === 'refresh_token' ? refresh_token = arr[1] : refresh_token = '';
                    ;
                })
                let data = {access_token, refresh_token};
                this.setTokenAct(data);
            },
            /**/
            getUserInfo() {
                let obj = {access_token: this.access_token};
                this.$axios({
                    url: url.getUserInfo,
                    params: obj,
                }).then(res => {
                    console.log(res);
                    if (res.status === 200) {
                        let data = {
                            userId: res.data.id,
                            userName: res.data.login,
                            userImg: res.data.avatar_url,
                            userEmail: res.data.email
                        };
                        this.setLoginUserAct(data);
                    }
                }).catch(err => {
                    console.log(err);
                })
            },


        },

        async created() {
            await this.getCookieData();
            await this.getUserInfo()
        },
        mounted() {

            this.setClientHeight();
        }
    }

</script>

